import React, {setState} from 'react';

class WeatherData extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          temperature: []
        };
    }

    componentDidMount() {
        fetch("https://api.openweathermap.org/data/2.5/onecall?lat=33.441792&lon=-94.037689&exclude=hourly,daily&units=metric&appid=e937bb61987a79d09b7604a3375a9941")
          .then(res => res.json())
          .then(
            (result) => {
              this.setState({
                isLoaded: true,
                temperature: result.current.temp
              });
            },
            (error) => {
              this.setState({
                isLoaded: true,
                error
              });
            }
          )
      }
    
      render() {
        const { error, isLoaded, temperature } = this.state;
        if (error) {
          return <div>Ошибка: {error.message}</div>;
        } else if (!isLoaded) {
          return <div>Загрузка...</div>;
        } else {
          return (
            <h1>{temperature}</h1>
          );
        }
      }
}

export default WeatherData;